import React, { useState } from 'react';
import './App.css'

function GetData() {
  let [obj, update] = useState([]);
  fetch('https://api.covid19india.org/data.json')
    .then(req => req.json())
    .then(res => update(res.statewise.map(state => {
        return ({
          "confirmed" : new Intl.NumberFormat('en-IN').format(state.confirmed),
          "recovery" : new Intl.NumberFormat('en-IN').format(state.recovered),
          "deaths" : new Intl.NumberFormat('en-IN').format(state.deaths),
          "state" : (state.state === 'Total') ? 'India (total)' : state.state,
          "active" : new Intl.NumberFormat('en-IN').format(state.active),
          "updatedAt" : state.lastupdatedtime,
          "Note" : state.statenotes
        })
      })
    ))
    .catch(err => console.log(err))
  return obj;
}

function Stats() {
  const stats = GetData().map(state => 
    <div className="card m-2">
      <div className="card-header text-center"><h4>{state.state}</h4></div>
        <div className="card-body">       
          <table className="table-responsive text-light">
            <td className="card-body bg-dark">Confirmed<h1 className="badge badge-primary">{state.confirmed}</h1></td>
            <td className="card-body bg-dark">Cured <h1 className="badge badge-success">{state.recovery}</h1></td>
            <td className="card-body bg-dark">Deaths <h1 className="badge badge-danger">{state.deaths}</h1></td>
            <td className="card-body bg-dark">Active <h1 className="badge badge-warning">{state.active}</h1></td>
          </table>
        </div>
      <div className="card-footer text-muted m-4"> last update: {state.updatedAt} </div>
      {(state.Note !== "")? <p className="container"><i dangerouslySetInnerHTML={{__html: String(state.Note).replace(/[*|-]/g, ' ')}}></i></p> : ""}
    </div>
  );
  return <ul className="list container" Style="text-align:center">{stats}</ul>;
}
function Header() {
  return(
    <div>
      <header className="card-header bg-dark text-light">
        <h4 className="text-center">COVID-19 tracker (India)</h4>
      </header>
      <div className="card m-2">
        <h3 className="text-center card-title"><i>"prevention is better than cure"</i></h3>
        <p className="card-body container">
          Being said, as much as possible be at home and halt the spread of Coronavirus. As you can see, this web app shows live statistics of coronavirus cases in India (using this <a href="https://api.covid19india.org/" className="text-primary"><i>API</i></a>) and these stats may or may not be accurate, so please consider referring to official sources (eg: <a className="text-primary" href="http://goo.gl/search/Covid-19+stats+of+India"><i>Google</i></a>) for the accurate data. This web app includes all states and union territories and some data that has to be added to the list. Anyway, vaccine is yet to be discovered and approved by some tests; only thing that we could do now is staying at home and keeping social distance (keep washing hands often, ofcourse).
        </p>
      </div>
    </div>
  );
}
function App() {
  return(
    <div> 
      <Header /> 
      <Stats /> 
      <footer className="card-footer bg-dark text-center text-light">
        Â©2020 <a className="text-primary" href="https://github.com/vinaykumargb">Vinay_GB</a>
      </footer>
    </div>
  ); 
}

export default App;
